import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import userService from '../../utils/userService';

class SignupForm extends Component{
    constructor(props){
    super(props);
    this.state = {
        name: '',
        email: '',
        password: '', 
        passwordConf: '',
    };
}
    handleChange = (field, e) => {
        this.props.updateMessage('');
        this.setState({
            [field]: e.target.value
        });
    }
    handleSubmit = (e) => {
        e.preventDefault();
        userService.signup(this.state)
        .then(()=> {
            this.props.handleSignup();
            this.props.history.push('/')
        })
        .catch(err => this.props.updateMessage(err.message));
    }
    isFormInvalid() {
        return !(this.state.name && this.state.email && this.state.password === this.state.passwordConf);
    } 
    render() {
        return (
            <div>
                <header className='App-header'>Sign Up</header>
                    <form className='form-horizontal' onSubmit={this.handleSubmit} >
                        <div className='form-group'>
                            <div className='col-sm-12'>
                                <input type='text' className='form-control' placeholder='Name' value={this.state.name} onChange={(e) => this.handleChange('name', e)} />
                            </div>
                        </div>
                        <div className='form-group'>
                            <div className='col-sm-12'>
                                <input type='email' className='form-control' placeholder='E-mail' value={this.state.email} onChange={(e) => this.handleChange('email', e)} />
                            </div>
                        </div>
                        <div className='form-group'>
                            <div className='col-sm-12'>
                                <input type='password' className='form-control' placeholder='Password?' value={this.state.password} onChange={(e) => this.handleChange('password', e)} />    
                            </div>
                        </div>
                        <div className='form-group'>
                            <div className='col-sm-12'>
                                <input type='password' className='form-control' placeholder='Confirm Password' value={this.state.passwordConf} onChange={(e) => this.handleChange('passwordConf', e)} />
                            </div>
                        </div>
                        <div className='form-group'>
                            <div className='col-sm-12 text-center'>
                                <button className='btn btn-secondary' disabled={this.isFormInvalid()}>Sign Up</button>&nbsp;&nbsp;
                                <Link to='/'>Cancel</Link>
                            </div>
                        </div>
                    </form>
            </div>  
        );
    }
};

export default SignupForm; 


